  .newsletter-section {
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: .5rem;
    flex-shrink: 30%;
    color: $white;
    position: relative;
    //margin: 0 auto 0 0;
    margin: auto;
    min-height: 35rem;
    &::before {
      @include pseudo-el;
      width: 20rem;
      height: 20rem;
      border-radius: 50%;
      margin-top: 20rem;
      margin-left: 41vw;
      z-index: 1;
      background: $bg-transparent-c;
    }
    &::after {
      @include pseudo-el;
      width: 15rem;
      height: 15rem;
      border-radius: 50%;
      margin-top: -3rem;
      margin-left: 36vw;
      z-index: 1;
      background: $bg-transparent-c;
    }
    .newsletter-text {
      min-width: 8rem;
      text-shadow: 0px 0px 5px rgb(0, 0, 0);
      z-index: 5;
      font-size: 1.75rem;
      margin: 0 0 .5rem 0;
    &:nth-child(2){
      font-size: 1.25rem;
      font-weight: lighter;
    }
    }
    
  }